<template>
  <div class="container">
    <quote-header></quote-header>
    <quote-progress
      :currentCount="quotes.length"
      :quoteLimit="quoteLimit"
    ></quote-progress>
    <hr />
    <quote-input @submited="addQuote"></quote-input>
    <hr />
    <quote-list 
		:quotes="quotes"
		@removing="removeQuote"></quote-list>
  </div>
</template>

<script>
import QuoteHeader from './components/QuoteHeader';
import QuoteProgress from "./components/QuoteProgress";
import QuoteInput from "./components/QuoteInput";
import QuoteList from "./components/QuoteList";

let quotes;
if ( localStorage.getItem('quotes') ) {
  quotes = JSON.parse(localStorage.getItem('quotes'));
} else {
  quotes = [];
}

export default {
  name: "App",
  components: {
    QuoteProgress,
    QuoteInput,
    QuoteList,
    QuoteHeader,
	},
	

  data() {
    return {
      quotes,

      quoteLimit: 10,
    };
	},
	
	methods: {
		addQuote(e) {
			if ( this.isPosibleToAdd() ) {
        this.quotes.push(e);
        localStorage.setItem('quotes', JSON.stringify(this.quotes));
			} else {
				alert('Դուք սպառեցիք Ձեր գրությունների սահմանը: Կարող եք ջնջել մի քանիսը, նոր գրառումների համար տեղ ազատելու համար')
			}
		},

		removeQuote(e) {
      this.quotes.splice(e, 1);
      localStorage.setItem('quotes', JSON.stringify(this.quotes));
		},

		isPosibleToAdd() {
			return this.quoteLimit > this.quotes.length;
		}
	}
};
</script>

