<template>
  <div class="progress">
    <div
      class="progress-bar"
      role="progressbar"
      v-bind:style="progressStyle"
      aria-valuenow="25"
      aria-valuemin="0"
      aria-valuemax="100"
    >
      {{ currentCount }} / {{ quoteLimit }}
    </div>
  </div>
</template>

<style lang="scss">
.progress {
  background-color: #ccc;
  width: 50%;
  min-width: 250px;
  margin: 0 auto;
}
</style>

<script>
export default {
  props: ["currentCount", "quoteLimit"],
  data() {
    return {};
  },

  computed: {
    progressStyle() {
      return {
          width: this.currentCount * 10 + "%"
      }
    },
  },
};
</script>