<template>
    <textarea id="quote" ref="text" class="form-control"  placeholder="Ինչ գրառում եք ցանկանում ավելացնել">
    </textarea>
    <button @click="addQuote" class="btn btn-success">Ավելացնել</button>
</template>


<script>
export default {
    emits: ['submited'],
    methods: {
        addQuote() {
            let text = this.$refs.text.value;
            this.$emit('submited', text);
            this.$refs.text.value = "";
            this.$refs.text.focus();
        }
    },
}
</script>
<style lang="scss">
    #quote {
        margin: 0 auto;
        margin-bottom: 1rem;
        min-width: 250px;
        width: 80%;
        height: 80px;
    }

    button {
        display: block !important;
        margin: 0 auto;
    }
</style>