<template>
  <div class="quote-list">
    <div v-if="quotes.length == 0" class="alert alert-warning" role="alert">
      Գրառումներ չկան: Օգտվեք վերևում գտնվող մուտքագրման վահանակից, նոր գրառումներ ավելացնելու համար:
    </div>
    <div v-for="(quote, key) in quotes" :key="key" class="quote-cart">
      <p>{{ quote }}</p>
      <div class="overlay">
        <span @click="removeQuote(key)">X Ջնջել</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["quotes"],
  emits: ["removing"],

  methods: {
    removeQuote(key) {
      this.$emit("removing", key);
    },
  },
};
</script>

<style lang="scss">
.quote-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 2rem;

  .quote-cart {
    flex: 0 0 30%;
    padding: 2rem 0.5rem;
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0 8px 15px rgba(204, 204, 204, 0.37);
    min-height: 300px;
    margin-bottom: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;

    .overlay {
      position: absolute;
      background-color: rgba(255, 0, 0, 0.685);
      left: 0;
      bottom: 0;
      width: 0%;
      height: 20%;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      transition: 0.3s width ease-in-out;

      span {
        color: #fff;
        font-weight: 800;
        cursor: pointer;
        opacity: 0;
        transition: 0.4s opacity ease-in-out;
      }
    }

    &:hover {
      .overlay {
        width: 100%;
        span {
          opacity: 1;
        }
      }
    }
  }
}


@media (max-width: 800px) {
    .quote-list {
        flex-direction: column;
        .quote-cart {
            flex: 0 0 100%;
            min-height: 100px;
        }
    }
}
</style>